* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

body {
  color: #000;
  background-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  color: inherit;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: block;
}

a:visited {
  outline: none;
  text-decoration: none;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  display: block;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

label, input, textarea {
  display: block;
}

.container {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 32px;
  }
}

.loader {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background-color: #e5e5e5;
  justify-content: center;
  align-items: center;
  transition: all 2.5s, visibility 2.5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader-run {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 95px;
  height: 95px;
  border: 15px solid #fff;
  border-top-color: #ff6b08;
  border-radius: 50%;
  animation: .8s infinite loading;
}

@keyframes loading {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

